* {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  color: #2f4f4f;
  box-sizing: border-box;
  font-size: 20px;
  line-height: 1.15;
}

.people {
  --columns: 1;
  grid-template-columns: repeat(var(--columns), 220px);
  justify-content: center;
  gap: 4px 10px;
  padding: 30px 35px;
  display: grid;
}

@media (width >= 500px) {
  .people {
    --columns: 2;
  }
}

@media (width >= 1000px) {
  .people {
    --columns: 4;
  }
}

.person {
  transform-origin: center;
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  padding: 10px 0 16px;
  transition: transform .1s;
  display: flex;
}

.person:hover {
  z-index: 2;
  background-color: #eee;
  transform: scale(1.2);
}

.person:hover .extra {
  opacity: 1;
}

.person:hover .photo img {
  border-radius: 60px;
}

.person .details {
  text-align: center;
}

.person .sport:after {
  content: ",";
}

.person .pay:before {
  content: "$";
}

.person .pay:after {
  content: "M";
}

.person .extra {
  opacity: 0;
  color: #000;
  margin-top: 9px;
  font-size: 18px;
}

.person .name {
  text-align: center;
  text-decoration: none;
}

.person .photo {
  z-index: 0;
  width: 200px;
  height: 200px;
  margin-bottom: 8px;
  padding: 0 10px;
  position: relative;
}

.person .photo img {
  object-fit: cover;
  border-radius: 50%;
  width: 100%;
  transition: border-radius 1s;
}

.person .photo > span {
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}
